<template>
  <tr>
    <td data-title="Index">{{ index+1 }}</td>
    <td data-title="Thumbnail">
      <div class="thumbnail">
        <img v-lazy="item.content.image" />
      </div>
    </td>
    <td data-title="Name">
      <div class="name">
        <div class="name_zh">{{ item["zhName"] }}</div>
        <div class="name_en">{{ item.name }}</div>
      </div>
    </td>
    <td data-title="Qty">{{ item.variations.length }}</td>
    <td data-title="Variations">
      <ul class="variations" v-if="item.variations.length > 0">
        <li v-for="(obj, i) in item.variations" :key="i">
          <!-- {{obj.tid}} -->
          <!-- v-show="(item.variations.length>1&&obj.content.filename.slice(-1)=='0')||item.variations.length==1" -->
          <!-- <el-tooltip placement="top">
            <div slot="content">
              <div style="height:40px:width:40px">
                <img :src="obj.content.image" style="height:100%:width:100%" />
              </div>
              <div style="text-align:center">{{ obj.content.bodyColor_zh }}</div>
          </div>-->

          <div
            class="variation"
            :class="{'variation--isOwnd':status[obj.idx]}"
            @click="toggleVariation(obj.idx)"
          >
            <div class="variation__thumbnail">
              <img  v-lazy="obj.content.image" />
            </div>

            <div class="variation__text">{{obj.content.bodyColor_zh}}</div>
          </div>

          <!-- </el-tooltip> -->
        </li>
      </ul>
    </td>
  </tr>
</template>

<script>
export default {
  name: "CardRow",
  props: {
    item: {
      type: Object,
      default: () => ({})
    },
    index: {
      type: Number,
      default: 0
    }
  },
  computed: {
    bookFile: {
      get() {
        return this.$store.state.bookFile;
      },
      set(value) {
        this.$store.commit("UPDATE_BOOKFILE", value);
      }
    },
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      }
    }
  },
  data() {
    return {};
  },

  mounted() {},
  methods: {
    toggleVariation(order) {
      // console.log(order);
      // console.log(this.status);
      const currentStatus = this.status[order];
      this.status[order] = currentStatus ? 0 : 1;
      this.$store.commit("UPDATE_STATUS", this.status);
    }
  }
};
</script>

