<template>
  <div class="books main">
    <el-button @click="save()">Save</el-button>
    <el-button @click="onShareClick()">Share</el-button>
    <!-- <ul>
      <li
        v-for="(item, index) in listDatas"
        :key="index"
        style="list-style:none"
      >
        <card :item="item" :index="index" />
      </li>]
    </ul>-->

    <div class="table-responsive-vertical shadow-z-1">
      <!-- Table starts here -->
      <table id="table" class="table table-hover table-mc-light-blue">
        <thead>
          <tr>
            <th>序號</th>
            <th>縮圖</th>
            <th>名稱</th>
            <th>數量</th>
            <th>種類</th>
          </tr>
        </thead>
        <tbody>
          <card v-for="(item, index) in listDatas" :key="index" :item="item" :index="index" />
        </tbody>
      </table>
    </div>

    <el-dialog
      title="分享碼連結"
      :visible.sync="isShareDialogVisible"
      width="30%"
      style="min-width: 400px;"
    >
      <span>請複製連結進行分享</span>
      <div @click="handleClipboard(shareUrl, $event)" class="share-box">{{shareUrl}}</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isShareDialogVisible = false">關閉</el-button>
        <el-button type="primary" @click="handleClipboard(shareUrl, $event)">複製</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import card from "./card.vue";
import datas from "@/output.js";
import Codes from "@/utils/codes";
import clipboard from "@/utils/clipboard";
// const datas = []

export default {
  name: "Books",
  components: {
    card
  },
  data() {
    return {
      datas,
      isShareDialogVisible: false,
      shareUrl: ""
    };
  },
  computed: {
    bookFile: {
      get() {
        return this.$store.state.bookFile;
      },
      set(value) {
        this.$store.commit("UPDATE_BOOKFILE", value);
      }
    },
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      }
    },
    listDatas() {
      return this.$store.state.listDatas;
    }
  },
  mounted() {
    const hash = this.$route.params.hash;
    if (hash) {
      this.$store.commit("READ_STATUS", hash);
    } else {
      this.$store.commit("LOAD_STATUS");
    }
  },
  methods: {
    handleClipboard(text, event) {
      clipboard(text, event);
    },
    save() {
      this.$store.commit(
        "SAVE_STATUS",
        Codes.generateHash(this.status.join(""))
      );

      this.$message({
        showClose: true,
        message: "儲存成功，僅於此瀏覽器能夠讀取",
        type: "success"
      });
    },
    onShareClick() {
      Codes.generateURL(this.status.join(""), url => {
        // window.prompt("Copy to clipboard: Ctrl+C", `${url}`);
        this.isShareDialogVisible = true;
        this.shareUrl = url;
      });
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.main {
  background-color: #f5f5f5;
  padding: 50px 30px;
}

.books {
  ul {
    margin: 0;
  }
}
.share-box {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.24);
  cursor: pointer;
  border-radius: 3px;
  padding: 20px;
  margin: 10px 0;
  background-color: #f9fbfb;
}
</style>
